import React, { createContext, Component } from 'react'
import { Product } from '../components/Product'

interface CartItem {
    product: Product,
    quantity: number
}

export interface ProviderState {
    cartItems: CartItem[]
}

export interface ContextState extends ProviderState {
    addProductToCart: (product: Product) => void
}

export const CartContext = createContext<ContextState>({
    cartItems: [],
    addProductToCart: (product: Product) => {
        console.log(("error while adding " + product.name + " to the shit")
        )
    },
})


export const CartConsumer = CartContext.Consumer
export class CartProvider extends Component<{}, ProviderState> {

    constructor(props: {}) {
        super(props)
        this.state = {
            cartItems: []
        }
    }

    addProductToCart = (product: Product) => {
        const theCart: CartItem[] = Object.assign([], this.state.cartItems)
        theCart.push({ product: product, quantity: 1 })

        this.setState({ cartItems: theCart })
    }

    render() {
        return (
            <CartContext.Provider value={{
                ...this.state,
                addProductToCart: this.addProductToCart,
            }}>
                {this.props.children}
            </CartContext.Provider>
        )
    }
}